import { Component, Output, EventEmitter, OnChanges, OnInit } from '@angular/core';

import { CusExplStatus } from './../constant/target-custom.codeValue';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CusOperationHttpService } from './../../../../http/custom-operation/custom-operation.http.service';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { OpenExploreBan } from "app/pages/tzb/custom/custom-information/advanced-search/add-search/open-search-conditions/bean/open-explore.bean";

@Component({
  selector: 'app-open-search-conditions',
  templateUrl: './open-search-conditions.component.html',
  styleUrls: ['./open-search-conditions.component.scss'],
  providers: [Commfunc, CusOperationHttpService]
})
export class OpenSearchConditionsComponent implements OnInit {

   /**
     * 选择后的值
     */
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    //bean
    public openExploreBan: OpenExploreBan = new OpenExploreBan();
    //码值
    public cusExplStatus: any[] = CusExplStatus;//列表中的状态（条件状态）
    //日期
    public ch = CALENDAR_CN;//日期转化中文
    public creatTimeStart: string = '';//探索创建时间起
    public creatTimeEnd: string = '';//探索创建时间止
    //单选选中(探索ID)的值
    public custExplId: string = "";
    //总条数
    public custExpToalNum: number;
    //表数据
    public custExplTable: any = [];
    //提示信息  
    public msgs: any = [];

    constructor(
        public commfunc: Commfunc,
        public httpService: CusOperationHttpService
    ) { }


    //选择模板按钮
    ngOnChanges() {
        this.queryClick();
    }

    //选择模板按钮
    ngOnInit() {
        this.openExploreBan.pageSize = 10;
        this.openExploreBan.pageNum =1;
        this.queryClick();
    }

    //查询按钮
    queryClick() {
        this.openExploreBan.creatTimeStart = this.commfunc.transDateN(this.creatTimeStart);
        this.openExploreBan.creatTimeEnd = this.commfunc.transDateN(this.creatTimeEnd);
        this.httpService.queryMyCustAdvanceSeachList(this.openExploreBan).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.custExplTable = data.custExplList;
                this.custExpToalNum = data.totalCount;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '探索条件结果', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '探索条件结果', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '探索条件结果', detail: '调用服务失败！' });
        })
    }


    //列表中的单选按钮
    checkRadioValue(event, item) {
        this.custExplId = item.custExplId;
    }
    //确定按钮（将探索ID带到父页面（新增目标客户探索））
    confirmClick() {
        this.outValue.emit(this.custExplId);
    }

    //分页
    paginateExplo(event) {
        //每页显示的条数
        this.openExploreBan.pageSize = event.rows * 1;
        //当前页
        let currentPage = event.page + 1;
        this.openExploreBan.pageNum = currentPage;
        //调用查询的方法
        this.queryClick();
        this.openExploreBan.pageNum = 1;
    }

    //重置按钮
    resetClick() {
        this.openExploreBan = new OpenExploreBan();
        this.creatTimeStart = null;
        this.creatTimeEnd = null;
    }

}
