import { Component} from '@angular/core';
import { CusOperationHttpService } from './../../../http/custom-operation/custom-operation.http.service';
import { Message } from "primeng/primeng";
import { ActivatedRoute } from "@angular/router";
/**
 * 前后台联动管理-我的营销信息
 */
@Component({
  selector: 'app-wait-do-detail',
  templateUrl: './wait-do-detail.html',
  styleUrls: ['./wait-do-detail.scss']
})
export class WaitDoDetailComponent{
    display_0 : boolean = false;
    first;
    pageSize: number = 10;
    pageNum: number;
    total: number;//总条数

    isFontAfter : boolean = true
    msgs: Message[] = []; //提示信息
    location_params;
    item:any=[];//接收后台数据
    itemOther:any=[];//接收后台数据
    statusId:string;
    addInValue: any;
    tableList: any = [];

    constructor(
        public cusOperationHttpService: CusOperationHttpService,
        public route : ActivatedRoute
    ) {
        // 获取路由参数
        this.location_params = this.route.snapshot.params;
  
        let params = {
            custRequestId:this.location_params.custRequestId,
            workId:this.location_params.workId,
            workType:this.location_params.workType       
        }
        this.showInfo(params);
    }

    showInfo(params){
        this.cusOperationHttpService.frontBackLinkageInfoDetailQry(params).subscribe(data=>{
          if(data.returnCode.code == 'success'){
            if (data.statusId =='0') {
              this.statusId = '待分配';
            }else if (data.statusId =='1') {
              this.statusId = '已认领';
            } else if (data.statusId =='2') {
              this.statusId = '已分配';
            } else if (data.statusId =='3') {
              this.statusId = '已失效';
            }
            this.item=data;
            if (data.taskId) {
              let param = {
                taskId:data.taskId,
                pageNum: 1,
                pageSize: 10         
              }
              this.callBackData(param);
            } else {
              this.itemOther = {
                status:'否',
                claimCreatedDate:'',
                contactStatus:'',
                contactDate:''
              }
            }
          }
        },error=>{
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail:'调用服务失败'});
        })
      }
    
      callBackData (param) {
        this.cusOperationHttpService.queryCustContactProgram(param).subscribe( data =>{
          if(data.returnCode.code == 'success'){
            if (data.pageList[0].contactStatus == '1') {
              this.itemOther = {
                status:'是',
                claimCreatedDate:data.pageList[0].claimCreatedDate,
                contactStatus:'否',
                dealDate:data.pageList[0].dealDate
              }
            }
            if (data.pageList[0].contactStatus == '2') {
              this.itemOther = {
                status:'是',
                claimCreatedDate:data.pageList[0].claimCreatedDate,
                contactStatus:'是',
                dealDate:data.pageList[0].dealDate
              }
            }
          }else{
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        },error=>{
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail:'调用服务失败'});
        })
      }

  //新建联系计划页面
  newPlan_1() {
    this.tableList = [];
    this.tableList.push(this.item)
    this.addInValue = this.tableList;
    // this.new_arr1 = [];
    // this.new_arr1.push({ custRequestId: this.tableListAllot[0].custRequestId });
    this.display_0 = true;
  }

  // 关闭新增窗口
  addCall(event) {
    if (!event.stu) {
      this.display_0 = event.stu;
      this.fontAfterUpdateCustRequestWorkEffort(event);
    }
  }
  
  //前后台联动时用到的服务
  fontAfterUpdateCustRequestWorkEffort(event) {
    let params = {
      custRequestIdList: [{custRequestId : this.location_params.custRequestId}],
      contactId: event.workEffortId
    }
    let ob = this.cusOperationHttpService.UpdateCustRequestWorkEffort(params);
    ob.subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });

      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }
  
}
