  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public confirmationService: ConfirmationService,
    public route: ActivatedRoute,
    public router: Router,
  ) {
    this.codeValues();
    this.lxzt = this.code['Lxzt'];
    this.contactWay = this.code['ContactWays'];
    this.contactResultMZ = this.code['ContactResultMZ'];
    this.contactResultSF = this.code['ContactResultSF'];
    this.contactResultZB = this.code['ContactResultZB'];

    this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    if (this.user.orgId) {
      this.orgId = this.user.orgId;
    }
    if (this.user.userId) {
      this.userId = this.user.userId;
    }
  }

  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret;
  }
  ngOnInit() {
    this.dealTaskBean.ownerUserName = JSON.parse(this.commfunc.getSessionDataCH('chName')).userName;
    this.dealTaskBean.ownerUser = this.userId;
    this.dealTaskBean.ownerOrgName = JSON.parse(this.commfunc.getSessionDataCH('chName')).orgName;
    this.dealTaskBean.ownerOrg = this.orgId;
    this.workEffortId = this.route.snapshot.params.workEffortId;
    this.contactId = this.route.snapshot.params.contactId;
    //查询待办详情
    let param = {
      workEffortId: this.workEffortId,
      contactId: this.contactId,
      currentStatusId: '',
    }
    this.httpService.queryConWorkEffrotDetail(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.currentStatusId = data.currentStatusId;
        for (let item in data) {
          this.dealTaskBean[item] = data[item];
        }
        this.planContactTime = new Date(this.dealTaskBean.planContactTime)
        this.searchIp();
        if (this.dealTaskBean.currentStatusId == '1' || this.dealTaskBean.currentStatusId == '3') {
          this.isBtnTrue = true;
          this.displayDealResultByCantactTheme();
        } else {
          for (let item in data) {
            this.dealTaskBean['contactResult'] = data['contactResult'];//联系结果
            this.dealTaskBean['remarks'] = data['contactRemark'];//联系备注
            this.dealTaskBean['amtDate'] = data['contactRetime'];//联系结果时间
            this.dealTaskBean['amt'] = data['contactAmount'];//联系结果金额
            this.dealTaskBean['planTime'] = data['planContactTime']
          }
          this.isBtnTrue = false;
          this.doExpandShow();
        }
      } else {
        this.msgs = [],
          this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [],
        this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  //判断跳转ip
  searchIp() {
    let params = {
      custNo: this.dealTaskBean.custNo
    }
    this.httpService.selectCustBriefInfo(params).subscribe(data => {
      if (data.dataSourceId == 'BR00A') {
        this.HTTPIPDress = TZB_HTTP_XIN;
      } else if (data.dataSourceId == 'BR00B') {
        this.HTTPIPDress = TZB_HTTP_XIN_C;
      } else if (!data.dataSourceId || data.dataSourceId == '' || data.dataSourceId == undefined || data.dataSourceId == null) {
        this.HTTPIPDress = TZB_HTTP_XIN;
      }
    });
  }
  //展示处理结果
