  //日期转换
  transDateA(value) {
    if (!value) {
      return ''
    }
    let year = value.substring(0, 4)
    let month = value.substring(4, 6)
    let data = value.substring(6, 8)
    let newtime = year + "-" + month + "-" + data;
    return newtime;
  }
